import React, { useState, useEffect } from 'react';
import { gsap } from 'gsap';
import artistData from '../artist.js'; // artist.js에서 데이터 가져오기
import '../style/artist.css'; // artist.css 스타일 파일 import

function Artist() {
  const [expandedItem, setExpandedItem] = useState(null); // 클릭된 항목의 인덱스를 추적하는 상태

  useEffect(() => {
    const items = document.querySelectorAll('.item');

    const expand = (item, i) => {
      items.forEach((it, ind) => {
        if (i === ind) return;
        it.clicked = false;
        gsap.to(it, {
          width: '15vw',
          duration: 2,
          ease: 'elastic(1, .6)',
        });
      });

      item.clicked = !item.clicked;
      gsap.to(item, {
        width: item.clicked ? '25vw' : '15vw',
        duration: 2.5,
        ease: 'elastic(1, .3)',
      });
    };

    items.forEach((item, i) => {
      item.clicked = false;
      item.addEventListener('click', () => {
        expand(item, i);
        setExpandedItem(item.clicked ? i : null); // 클릭된 이미지의 인덱스를 설정합니다.
      });
    });
  }, []); // 한 번만 실행되어야 합니다.

  return (
    <div className="my-5">
      <h1 className="my-5">ARTIST</h1>
      <p>사진을 클릭하면 아티스트의 정보를 볼 수 있습니다.</p>
      <div className="group justify-content-center">
        {/* 상단 세 명의 아티스트 */}
        <div className="artist-row">
          {artistData.slice(0, 3).map((member, index) => (
            <div
              key={index}
              className={`item ${expandedItem === index ? 'expanded' : ''}`}
              style={{ backgroundImage: `url(${member.image})` }}
            >
              {expandedItem === index && ( // 클릭된 경우에만 정보를 표시합니다.
                <div className="artist-info">
                  <h5>{member.name}</h5>
                  <p>{member.age}</p>
                  <a href={member.instagram} target="_blank" rel="noopener noreferrer">
                    <img src="https://raw.githubusercontent.com/SSuaSea/shinee-fansite/5ac83b8b3a50c865d3ef02f7782367adfa6f394e/insta.svg" alt="Instagram" className="social-icon" />
                  </a>
                </div>
              )}
            </div>
          ))}
        </div>
        {/* 하단 두 명의 아티스트 */}
        <div className="artist-row">
          {artistData.slice(3, 5).map((member, index) => (
            <div
              key={index + 3} // 고유한 키를 생성하기 위해 추가
              className={`item ${expandedItem === index + 3 ? 'expanded' : ''}`}
              style={{ backgroundImage: `url(${member.image})` }}
            >
              {expandedItem === index + 3 && ( // 클릭된 경우에만 정보를 표시합니다.
                <div className="artist-info">
                  <h5>{member.name}</h5>
                  <p>{member.age}</p>
                  <a href={member.instagram} target="_blank" rel="noopener noreferrer">
                    <img src="https://raw.githubusercontent.com/SSuaSea/shinee-fansite/5ac83b8b3a50c865d3ef02f7782367adfa6f394e/insta.svg" alt="Instagram" className="social-icon" />
                  </a>
                </div>
              )}
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}

export default Artist;
