import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import "../style/login.css"; // CSS 파일 경로

const Login = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [loginCheck, setLoginCheck] = useState(false); // 로그인 상태 체크

  const navigate = useNavigate();

  const handleLogin = async (event) => {
    // 로그인 처리 로직
    event.preventDefault();
    await new Promise((r) => setTimeout(r, 1000));
    
    const response = await fetch(
      "/",
      {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          email: email,
          password: password,
        }),
      }
    );
    const result = await response.json();

    if (response.status === 200) {
      setLoginCheck(false);
      // Store token in local storage
      sessionStorage.setItem("token", result.token);
      sessionStorage.setItem("email", result.email); // userid를 저장
      sessionStorage.setItem("role", result.role); // role을 저장
      sessionStorage.setItem("storeid", result.storeId); // role를 저장
      console.log("로그인성공, 이메일주소:" + result.email);
      navigate("/"); // 로그인 성공시 홈으로 이동합니다.
    } else {
      setLoginCheck(true);
    }
  };

  return (
    <div className="my-5 text-center">
      <h1 className="login-title my-5">Login</h1>
      <div className="loginform-container">
        <form className="login-form" onSubmit={handleLogin}>
          <label htmlFor="username" className="form-label">이메일</label>
          <input
            type="text"
            id="username"
            className="form-input"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />

          <label htmlFor="password" className="form-label">비밀번호</label>
          <input
            type="password"
            id="password"
            className="form-input"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />

          {loginCheck && <label className="error-message">이메일 혹은 비밀번호가 틀렸습니다.</label>}
          
          <button type="submit" className="form-button">로그인</button>
        </form>
      </div>
    </div>
  );
};

export default Login;
