import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import '../layouts/header.css';

export default function Header() {
  const [activeTab, setActiveTab] = useState('');
  const [menuOpen, setMenuOpen] = useState(false);
  

  const handleTabClick = (tabName) => {
    setActiveTab(tabName);
    setMenuOpen(false); // 탭을 클릭하면 메뉴를 닫습니다.
  };

  const toggleMenu = () => {
    setMenuOpen(!menuOpen); // 메뉴를 열고 닫습니다.
  };

  return (
    <div className="header">
      <div className="logo">
        <Link to="/" className="myshinee text-neon">My SHINee</Link>
      </div>
      <div className={`menu-icon ${menuOpen ? 'open' : ''}`} onClick={toggleMenu}>
        <div className="menu-line"></div>
        <div className="menu-line"></div>
        <div className="menu-line"></div>
      </div>
      <nav className={`nav-links ${menuOpen ? 'open' : ''}`}>
        <Link to="/artist" className={`nav-link ${activeTab === 'Artist' ? 'active' : ''}`} onClick={() => handleTabClick('Artist')}>Artist</Link>
        <Link to="/toptrack" className={`nav-link ${activeTab === 'Top Track' ? 'active' : ''}`} onClick={() => handleTabClick('Top Track')}>Top Track</Link>
        <Link to="/history" className={`nav-link ${activeTab === 'History' ? 'active' : ''}`} onClick={() => handleTabClick('History')}>History</Link>
        <Link to="/fanletter" className={`nav-link ${activeTab === 'Fanletter' ? 'active' : ''}`} onClick={() => handleTabClick('Fanletter')}>Fanletter</Link>
      </nav>
      <div className="login-container">
        <Link to="/login" className="login-btn">Login</Link>
      </div>
    </div>
  );
}
