import React, { useEffect, useState, useRef } from 'react';
import axios from 'axios';
import { getToken } from '../auth'; // auth.js 파일의 경로에 따라 수정
import '../style/toptrack.css';

function TopTrack() {
  const [tracks, setTracks] = useState([]); // 트랙 데이터를 저장할 state 변수 선언
  const [currentTrack, setCurrentTrack] = useState(null); // 현재 재생 중인 트랙 상태
  const audioRef = useRef(null); // audio 요소 참조

  useEffect(() => {
    const fetchTopTracks = async () => {
      try {
        const accessToken = await getToken(); // getToken 함수 호출하여 액세스 토큰 발급
        if (!accessToken) {
          throw new Error('Failed to get access token'); // 액세스 토큰 발급 실패 시 에러 발생
        }
        const response = await axios.get(
          'https://api.spotify.com/v1/search',
          {
            headers: {
              Authorization: `Bearer ${accessToken}`,
            },
            params: {
              q: 'artist:SHINee',
              type: 'track',
              market: 'KR', // 한국(KR) 시장을 기준으로 데이터 요청
              limit: 21, // 최대 21개의 트랙 요청
            },
          }
        );
        console.log('API response:', response.data.tracks.items); // 응답 데이터 구조 확인.
        setTracks(response.data.tracks.items); // 응답 데이터에 따라 수정
      } catch (error) {
        // API 호출 중 에러 발생 시 처리
        console.error('Error fetching top tracks:', error.response ? error.response.data : error.message);
      }
    };

    fetchTopTracks();
  }, []);

  // 음악 재생 함수
  const playTrack = (previewUrl) => {
    if (currentTrack === previewUrl) {
      // 이미 재생 중인 트랙을 다시 클릭하면 멈추기
      setCurrentTrack(null);
      if (audioRef.current) {
        audioRef.current.pause(); // 재생 중인 오디오 멈춤
      }
    } else {
      // 새로운 트랙 재생
      setCurrentTrack(previewUrl);
      if (audioRef.current) {
        audioRef.current.src = previewUrl; // 새로운 오디오 소스 설정
        audioRef.current.play(); // 새로운 오디오 재생
      }
    }
  };

  useEffect(() => {
    const handleEnded = () => {
      setCurrentTrack(null);
    };

    const audioElement = audioRef.current;
    if (audioElement) {
      audioElement.addEventListener('ended', handleEnded);
    }

    return () => {
      if (audioElement) {
        audioElement.removeEventListener('ended', handleEnded);
      }
    };
  }, [currentTrack]);

  return (
    <div className="toptrack">
      <h1 className="my-5">SHINee's Top Tracks</h1>
      <ul className="track-list">
        {tracks.map((track) => (
          <li key={track.id} className="track-item">
            <img src={track.album.images[0].url} alt={track.album.name} /> {/* 앨범 썸네일 추가 */}
            <div className="track-info">
              <span className="track-name">{track.name}</span>
              <button onClick={() => playTrack(track.preview_url)}>
                {currentTrack === track.preview_url ? 'Stop' : 'Play!'}
              </button>
            </div>
          </li>
        ))}
      </ul>
      {/* 현재 재생 중인 트랙이 있을 때만 오디오 요소 표시 */}
      {currentTrack && (
        <audio ref={audioRef} controls autoPlay>
          <source src={currentTrack} type="audio/mp3" />
          Your browser does not support the audio element.
        </audio>
      )}
    </div>
  );
}

export default TopTrack;
